.hero {
  @media screen and (min-width: 320px) {
    padding-top: 32px;
    padding-bottom: 52px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 35px;
  }
}

.hero__section {
  position: relative; 
  padding-top: 34px;
  padding-bottom: 52px;

  @media screen and (min-width: 768px) {
    padding-top: 44px;
    padding-bottom: 52px;
  }

  @media screen and (min-width: 1440px) {
    padding-top: 12px;
    padding-bottom: 100px;
  }
}

.hero__cover {
  text-align: center;

  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    margin-bottom: 64px;
  }

  @media screen and (min-width: 1440px) {
    max-width: 624px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 68px;
  }
}

.hero__title {
  font-family: var(--secondary-font), sans-serif;
  @include font(18px, 400, 1.16, 0);

  @media screen and (min-width: 768px) {
    @include font(24px, 400, 1.16, 0);
  }

  @media screen and (min-width: 1440px) {
    @include font(48px, 400, 1.16, 0);
  }
}

.hero__list {
  display: flex;
  justify-content: center;

  @extend %list;

  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    margin-bottom: 85px;
  }

  @media screen and (min-width: 1440px) {
    margin-bottom: 142px;
  }
}

.hero__item {
  &:not(:last-child) {
    margin-right: 15px;
  }

  @media screen and (min-width: 768px) {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @media screen and (min-width: 1440px) {
    &:not(:last-child) {
      margin-right: 36px;
    }
  }
}

.hero__image-1 {
  border-radius: 0px 35px 0px 0px;
  box-shadow: (20px 20px 80px rgba(0, 0, 0, 0.3));

  @media screen and (min-width: 768px) {
    width: 113px;
  }

  @media screen and (min-width: 1440px) {
    width: 205px;
  }
}

.hero__image-2 {
  border-radius: 100px;
  box-shadow: (20px 20px 80px rgba(0, 0, 0, 0.3));

  @media screen and (min-width: 768px) {
    width: 113px;
  }

  @media screen and (min-width: 1440px) {
    width: 205px;
  }
}

.hero__image-3 {
  border-radius: 0px 25px;
  box-shadow: (20px 20px 80px rgba(0, 0, 0, 0.3));

  @media screen and (min-width: 768px) {
    width: 113px;
  }

  @media screen and (min-width: 1440px) {
    width: 205px;
  }
}

.hero__subtitle {
  font-family: var(--secondary-font), sans-serif;
  @include font(16px, 400, 1.18, 0);
  text-align: center;

  @media screen and (min-width: 768px) {
    @include font(18px, 400, 1.16, 0);
  }

  @media screen and (min-width: 1440px) {
    @include font(36px, 400, 1.16, 0);
  }
}

.bg-shadow-1 {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 71px;
    left: 117px;
  }

  @media screen and (min-width: 1440px) {
    top: 146px;
    left: 176px;
  }
}

.bg-shadow-2 {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 74px;
    right: 47px;
  }

  @media screen and (min-width: 1440px) {
    top: 151px;
    right: 32px;
  }
}

.bg-shadow-3 {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 356px;
    left: 47px;
  }

  @media screen and (min-width: 1440px) {
    top: 704px;
    left: 32px;
  }
}

.bg-shadow-4 {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 295px;
    right: 47px;
  }

  @media screen and (min-width: 1440px) {
    top: 583px;
    right: 32px;
  }
}

.bg-shadow-img-1 {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 72px;
}

.bg-shadow-img-2 {
  border-radius: 0px 0px 72px 0px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.bg-shadow-img-3 {
  border-radius: 72px;
  box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.25);
}

.bg-shadow-img-4 {
  border-radius: 72px 72px 0px 0px;
  box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.25);
}

.bg-shadow-button__link {
  display: none;

  @media screen and (min-width: 768px) {
    position: absolute;
    top: 295px;
    left: 118px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 71px;
    height: 74px;
    fill: var(--second-accent-color);

    background: #ffffff;
    box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.1);
    border-radius: 0px 0px 35px 0px;
  }

  @media screen and (min-width: 1440px) {
    top: 583px;
    left: 176px;

    width: 144px;
    height: 144px;
  }
}

.bg-shadow-button__icon {
  width: 16px;
  height: 16px;

  @media screen and (min-width: 1440px) {
    width: 32px;
    height: 32px;
  }
}
